import {
    createWebHashHistory,
    createRouter
} from 'vue-router';
import GramMain from '@/views/GramMain';
import GramSignup from '../views/GramSignup';

const routes = [
    /* {
        path: "/signup",
        name: "signup",
        component: GramSignup
    } */
    {
        path: "/",
        name: "signup",
        component: GramSignup
    }, {
        path: "/main",
        name: "main",
        component: GramMain
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router