        // Î™®Îì† Î≥ÄÏàòÎ•º ÎßåÎì§Îïå refÎ•º Íº≠ Ïç®ÏïºÌï®
        // vueÎäî Í∞íÏùÑ Í≥µÏú†ÌïòÎäî ÎûòÌçºÎü∞Ïä§ÌÉÄÏûÖÏù∏Îç∞ Í∑∏Í≤å Î≠êÎÉêÎ©¥ Î†åÎçîÎßÅÌï†Îïå htmlÏù¥Îûë Í∞íÏùÑ Í≥µÏú†ÌïòÎäîÍ≤å ÎûòÌçºÎü∞Ïä§ÌÉÄÏûÖÏûÑ
        // Ïù¥Î†áÍ≤å Ïª¥Ìè¨ÏßÄÏÖò APIÎäî Í∑∏ÎÉ• Î≥ÄÏàòÎ•º Ìò∏Ï∂úÌïòÍ∏∞ ÎïåÎ¨∏Ïóê refÎ•º Í∞êÏã∏ÏßÄ ÏïäÏúºÎ©¥ Î†åÎçîÎßÅÏù¥ ÏïàÎê®
        let follower = ref([]);

        onMounted(() => {
            axios
                .get("https://soominyim.github.io/vuestagram/follower.json")
                .then((a) => {
                    // Îç∞Ïù¥ÌÑ∞Î•º ÏàòÏ†ïÌïòÎ†§Î©¥ .valueÎ•º Íº≠ Ïç®ÏïºÌï®
